import React, {Component} from 'react'; 

class GamesListMake extends Component{
    constructor(props){ 
        super(props);
    }  

    render(){ 
        return( 
            <div> 
                <li> 
                    <a>{this.props.gameId} {this.props.gameName}</a>
                    <a>{this.props.playerId}{this.props.playerName}</a>
                </li>
            </div> 
        );
    }

}
export default GamesListMake;